
.clearfix:before, .clearfix:after {
  display: block;
  content: '';
  line-height: 0;
}
.clearfix:after{clear:both;}
.clearfix{*zoom:1;}
h2.title01 {text-align: center; color:rgb(54, 54, 54);border:none;}

.best_item, form{
  margin:0px auto;
  width:100%;

}
.best_item li{
  width:290px;
  list-style:none;
  float:left;
  text-align: center;
  border:2px solid #000;
  margin:0 10px;
  overflow:hidden;
}
.best_item img{
  width:100%;
  height:auto;
  object-fit:scale-down;
  transition:0.5s;
}
.best_item img:hover{filter:opacity(0.8);}
.best_item p {
  line-height:50px;
  font-weight:bold;
}

form{
  width:fit-content;
}